@media screen and ( max-width: 767px ) {
  .header {
    nav {
      a {} } }

  .index {
    h1 {
      font-size: 50px; } }

  .about-page {
    .header {
      .burger-button {
        .burger-container {
          span,
          &::after,
          &::before {
            background-color: #fff; } } } }

    main {
      p {
        line-height: 1.6; } }

    .about-section {
      margin-bottom: 100px;
      .section-content {
        flex-flow: column;
        .img-block {
          width: 100%;
          .img-container {
            width: 60%;
            top: 50px;
            margin-top: 250px; } }
        .desc-block {
          width: 100%;

          padding: 100px 80px 50px 80px;
