 }    ////     text-align: left

  .work-page {
    .project-section {
      .project-item {
        padding: 40px 40px; } } }

  .contacts-page {
    h1 {
      width: 100%;
      margin-right: 0px;
      // margin-bottom: 40px

      font-size: 32px; }

    .main-section {
      .title-row {
        margin-bottom: 60px;

        flex-flow: column;

 }        // text-align: center

      .contacts-row {
        width: 75%;
        margin-left: auto;

        flex-flow: column;

        // text-align: center

        .title {
          width: 100%;
          margin-right: 0px;
          margin-bottom: 40px; } }

      .contacts-block {
        width: 100%;

        align-items: flex-start; }

      .branches-block {
        .branch {
          &__title {
            display: inline-flex; } } }

      .soc-block {
        align-self: unset; }

      .emails-block {
        .email-item {
          display: flex;

          flex-flow: column;
          a {
            margin-top: 10px; } } } }

    .feedback-section {
      #feedback-form {
        .additional-info {
          flex-flow: column; }
        .project-info-item {
          width: 100%;
          &:not(:last-child) {
            margin-bottom: 40px; } } } } } }



