html,body,
div,span,p,a,
applet,
object,
iframe,
h1,h2,h3,h4,h5,h6,
blockquote,
pre,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,u,i,center,
dl,dt,dd,ol,ul,li,
fieldset,form,label,legend,
caption,table,tr,th,td,thead,tbody,tfoot,
article,aside,canvas,details,embed,
figure,figcaption,footer,header,hgroup,
menu,nav,
output,
ruby,
section,article,
summary,
time,
mark,
audio,video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  outline: none; }

/* HTML5 display-role reset for older browsers */
header,footer,section,article {}
aside,
details,
figcaption,
figure,
hgroup,
menu,nav {
  display: block; }

html {
  // -ms-overflow-style: none
  // scrollbar-width: none

  // &::-webkit-scrollbar
 }  //   width: 0

body {
  line-height: 1;
  font-family: 'Ubuntu', sans-serif;


  // transition: all .3s

  // -ms-overflow-style: none
  // scrollbar-width: none

  // &::-webkit-scrollbar
 }  //   width: 0

blockquote,
q {
  quotes: none; }

ol,ul {
  list-style: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a,
a:visited {
  text-decoration: none;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out; }

a:hover,
a:visited:hover {
  text-decoration: none; }

a:focus {
  outline: 0; }

input {
  outline: none; }

*:not(.slick-track):not(.slick-list) {
  // -webkit-transition: all .2s ease-in-out
 }  // transition: all .2s ease-in-out

body {
  &::-webkit-scrollbar {
    width: 0px; } }

.ovh {
  overflow: hidden; }

.header-indent {
  padding-top: 160px!important; }

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  @media (min-width: 576px) {
    max-width: 540px; }
  @media (min-width: 768px) {
    max-width: 720px; }
  @media (min-width: 992px) {
    max-width: 960px; }
  @media (min-width: 1200px) {
    max-width: 1140px; } }

 :root {
  --main: #707070;
  --hover: #9FC058; }

@import "_header.sass";
@import "index.sass";
@import "about.sass";
@import "contacts.sass";
@import "work.sass";

@import "adaptive/1200.sass";
@import "adaptive/992.sass";
@import "adaptive/768.sass";
